import { StatusBar } from 'expo-status-bar';
import React, { useState, useEffect } from 'react';
import {Button, Image, StyleSheet, Text, TouchableOpacity, View} from 'react-native';
import {AuthContext} from '../../contexts/AuthContext';
import LinearGrad from "../../components/LinearGrad";
import BasicButton from "../../components/buttons/BasicButton";

export default function ProfileStartScreen({navigation}) {
    const {logout} = React.useContext(AuthContext);

    //To next profile step
    const handleNext = async (e) => {
        e.preventDefault()
        navigation.navigate('ProfileStep1Screen')
    }

    //Disable Skip, it's now necessary to complete the profile to use the app
    // const handleSkip = async (e) => {
    //     e.preventDefault()
    // }

    return (
        <View style={styles.container}>
            <LinearGrad/>
            <Image
                style={styles.logo}
                source={{
                    uri:
                        "data:image/png;base64,"
                }}
            />
            <Text style={styles.textMiddle}>We need to complete your profile to find the best buddy for you.<br/><br/>This step is necessary to know you better.</Text>
            <View style={styles.content}>
                <TouchableOpacity style={[styles.button,styles.plain]} onPress={handleNext}>
                    <Text style={[styles.text,styles.plainText]}>Continue</Text>
                </TouchableOpacity>
                <TouchableOpacity style={[styles.button,styles.plain]}
                    name={'log-out'}
                    onPress={() => {
                        logout();
                    }}>
                    <Text style={[styles.text,styles.plainText]}>Logout</Text>
                </TouchableOpacity>
            </View>
        </View>
    );
}

const styles = StyleSheet.create({
    container: {
        flex: 1,
        backgroundColor: '#fff',
        alignItems: 'center',
        justifyContent: 'space-between',
        paddingLeft: 20,
        paddingRight: 20,
    },
    button : {
        height: 50,
        borderRadius: 100,
        alignSelf: 'stretch',
        alignItems: 'center',
        justifyContent: 'center',
        overflow: 'hidden',
        backgroundColor: 'white'
    },
    content: {
        alignSelf: 'stretch',
        height: 165
    },
    logo : {
        marginTop: 75,
        width: 150,
        height: 150
    },
    text : {
        fontWeight : 'bold',
        fontSize : 14
    },
    plain : {
        marginBottom: 15
    },
    plainText : {
        color: '#34CC98',
    },
    textMiddle: {
        color: 'white',
        textAlign: 'center',
        fontWeight: 'bold',
        fontSize : 16
    }
});