import { StatusBar } from 'expo-status-bar';
import React, { useState, useEffect } from 'react';
import {Button, Image, StyleSheet, Text, TouchableOpacity, View} from 'react-native';
import {AuthContext} from '../../contexts/AuthContext';
import BasicButton from "../../components/buttons/BasicButton";
import StepNum from "../../components/StepNum";

export default function ProfileStep2Screen({route,navigation}) {
    const {logout} = React.useContext(AuthContext);
    const [firstname, setFirstname] = useState(route.params.params.firstname);
    const [lastname, setLastname] = useState(route.params.params.lastname);
    const [gender, setGender] = useState('');

    return (
        <View style={styles.container}>
            <View style={styles.header}>
                <View style={styles.step}>
                    <StepNum number={2}/>
                </View>
                <Text style={styles.text}>Which one are you ?</Text>
            </View>
            <View style={styles.content}>
                <View style={styles.content2}>
                    <TouchableOpacity style={{marginRight:10}} onPress={() => setGender('male')}>
                        <Image
                            style={[styles.image, gender === 'male' ? styles.border : '']}
                            source={{
                                uri:
                                    "data:image/png;base64,"
                            }}
                        />
                        <Text style={styles.text}>Male</Text>
                    </TouchableOpacity>
                    <TouchableOpacity style={{marginLeft:10}} onPress={() => setGender('female')}>
                        <Image
                            style={[styles.image, gender === 'female' ? styles.border : '']}
                            source={{
                                uri:
                                    "data:image/png;base64,"
                            }}
                        />
                        <Text style={styles.text}>Female</Text>
                    </TouchableOpacity>
                </View>
                <View style={styles.contentMessage}>
                    <Text style={styles.message}>To give you a custom experience,<br/> we need some basic informations of you.</Text>
                </View>
            </View>
            <View style={styles.buttons}>
                <BasicButton data={'plain'}
                             onButtonClick={'ProfileStep3Screen'}
                             ButtonText={'Continue'}
                             disabled={!(gender)}
                             params={{firstname:firstname,lastname:lastname,gender:gender}}/>
            </View>
        </View>
    );
}

const styles = StyleSheet.create({
    container: {
        flex: 1,
        backgroundColor: '#fff',
        alignItems: 'center',
        justifyContent: 'space-between',
        paddingLeft: 20,
        paddingRight: 20,
    },
    border : {
        borderColor: '#34CC98',
    },
    content: {
        justifyContent: 'center',
    },
    content2 : {
        flexDirection: 'row',
    },
    contentMessage : {
        marginTop: 50,
    },
    message: {
        fontWeight : '600',
        fontSize : 16,
        marginTop: 30,
        textAlign: 'center',
    },
    buttons: {
        alignSelf: 'stretch',
        height: 115
    },
    text : {
        fontWeight : 'bold',
        fontSize : 14,
        marginTop: 30,
        textAlign: 'center',
    },
    label : {
        color: '#34CC98',
        fontSize: 12,
        marginBottom: 5
    },
    label2 : {
        color: '#34CC98',
        fontSize: 12,
        marginBottom: 5,
        marginTop: 15
    },
    input : {
        borderRadius: 30,
        borderWidth: 2,
        borderColor: '#34CC98',
        paddingLeft: 25,
        paddingRight: 25,
        paddingTop: 15,
        paddingBottom: 15
    },
    image: {
        width:150,
        height: 200,
        borderRadius: 25,
        borderWidth: 2,
        borderStyle : 'solid',
        borderColor : 'transparent',
    },
});